<template>
  <div>
    <!-- Your main content -->

    <div v-if="showOverlay" class="overlay" @click="hideOverlay">
      <div class="overlay-content">
        <p>Thanks</p>
        <div class = "thanks_section">
          <p>Photography : </p>
          <a href="https://www.instagram.com/epicmedia__?igsh=ZGNjOWZkYTE3MQ==" target="_blank">
            Epic Media <font-awesome-icon :icon="['fab', 'instagram']" />
          </a>
        </div>
        <div class = "thanks_section">
          <p>Makeup : </p>
          <a href="https://www.instagram.com/orchid__beautyclinic?igsh=ZGNjOWZkYTE3MQ==" target="_blank">
            Orchid Beauty Clinic <font-awesome-icon :icon="['fab', 'instagram']" />
          </a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome';

export default {
  components: {
    FontAwesomeIcon,
  },
  data() {
    return {
      showOverlay: false,
      lastScrollPosition: 0,
    };
  },
  methods: {
    hideOverlay() {
      this.showOverlay = false;
    },
    handleScroll() {
      const scrollPosition = window.scrollY || document.documentElement.scrollTop;

      if (scrollPosition > this.lastScrollPosition) {
        // Scrolling down
        if (scrollPosition + window.innerHeight >= document.documentElement.scrollHeight) {
          // Reached the bottom of the page
          this.showOverlay = true;
        }
      } else {
        // Scrolling up
        this.showOverlay = false;
      }

      this.lastScrollPosition = scrollPosition;
    },
  },
  mounted() {
    window.addEventListener('scroll', this.handleScroll);
  },
  beforeUnmount() {
    window.removeEventListener('scroll', this.handleScroll);
  },
};
</script>

<style scoped>
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  
}

.overlay-content {
  text-align: center;
  color: white;
}
.thanks_section{
  display: flex;
  align-items: center;
  color: white;

}
